.gallery {
    height: 100%;

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 85px 0 70px 0;
        height: 100%;
    }

    &__select {
        border: 1px solid #f3d1bf;
        color: #80695c;
        border-radius: 5px;
        padding: 10px;
        margin: 0 0 25px 0;

        &:disabled {
            border: 1px solid red;
            cursor: not-allowed;
        }
    }

    &__photos {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 30px;
    }

    &__card {
        grid-column: 4 span;
    }

    &__button {
        background-color: #fff;
        border: 1px solid transparent;
        color: #391400;
        font-weight: 900;
        border-radius: 5px;
        text-transform: uppercase;
        padding: 10px 25px;
        margin: 80px auto 0 auto;
        transition: all 0.2s linear;

        &:disabled {
            border: 1px solid red;
            cursor: not-allowed;
        }

        &:hover {
            border: 1px solid #ef6d58;
        }
    }

    &__message {
        color: #000;
        text-align: center;
    }

    &__preloader {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.gallery__nav.nav {
    margin: 0px 0px 60px 0;
    justify-content: center;

    .nav__link {
        color: #391400;
    }

    .nav__link.active.nav__link.active {
        color: #16cd53;
        cursor: pointer;
    }

    .nav__link.disabled {
        color: red;
        cursor: not-allowed;
    }
}

.card {
    position: relative;
    overflow: hidden;
    word-break: break-all;
    cursor: pointer;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: flex-end;
    height: 415px;

    &__image {
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
    }

    &__information {
        z-index: 2;
        margin: 10px 10px 50px 50px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        color: #fff;

        :not(:last-child) {
            margin: 0 0 20px 0;
        }
    }

    &__category {
        background-color: #fff;
        border-radius: 20px;
        color: #000;
        font-size: 14px;
        padding: 10px 15px;
        transition: all 0.2s linear;

        &:hover {
            color: #16cd53;
        }
    }

    &__name {
        font-size: 40px;
        font-weight: 600;
        height: 80px;
        line-height: 45px;
        overflow: scroll;
        color: #000;

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }

        &::-webkit-scrollbar-track {
            background-color: transparent;
        }

        &::-webkit-scrollbar-thumb {
            background-color: transparent;
        }

        &::-webkit-scrollbar-corner {
            background-color: transparent;
        }
    }
}

.card.active {
    outline: 2px solid greenyellow;
}